import { Action, ActionPanel, closeMainWindow, Detail, LaunchProps } from "@raycast/api";

export default function Command(props: LaunchProps<{ arguments: {
  text: string;
}}>) {
  return <Detail 
            markdown={props.arguments.text} 
            actions={
            <ActionPanel>
                <Action 
                title="Done"
                onAction={closeMainWindow}
                shortcut={{ modifiers: ["cmd"], key: "escape" }}
                />
            </ActionPanel>
            }
            />;
}
